


























import Vue from "vue";
import Header from "../components/Header.vue";
import Footer from "../components/Footer.vue";
import Reveal from "../components/Reveal.vue";
import api from "../api/index";
import IResp, { IArchive } from "../types/resp";

interface Idata {
  currPage: number;
  count: number;
  dir: IArchive[] | null;
}

export default Vue.extend({
  name: "Home",
  data(): Idata {
    return {
      currPage: 0,
      count: 0,
      dir: null
    };
  },
  created() {
    this.getData();
  },
  computed: {
    loading() {
      if (!this.dir) return true;
      else return false;
    }
  },
  methods: {
    async getData() {
      this.dir = null;
      const res = await api.getTable(this.currPage);
      this.setData(res.dir, res.count);
    },
    setData(dir: IArchive[], count: number) {
      this.dir = dir;
      this.count = count;
    },
    currentChange(val: number) {
      this.currPage = val - 1;
      this.getData();
    },
    sizeChange(val: number) {},

    tranTime(str: string) {
      return new Date(str).toLocaleDateString().replace(/\//g, "-");
    }
  },
  components: {
    Header,
    Footer,
    Reveal
  }
});
